html {
  font-size: 62.5%;
  position: relative;
  min-height: 100%;
}
html,
body {
  margin: 0;
  padding: 0;
  font-size: 1rem;
}
html::-webkit-scrollbar,
body::-webkit-scrollbar {
  width: 12px;
}
html::-webkit-scrollbar-thumb,
body::-webkit-scrollbar-thumb {
  background: white;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}
html::-webkit-scrollbar-track,
body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: whitesmoke;
}
* {
  box-sizing: border-box;
  outline: none;
}
button {
  cursor: pointer;
}
button:disabled {
  cursor: default;
}
.login_shell {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}
.login_container {
  position: absolute;
  padding: 15px 0px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
@media (max-width: 3000px) {
  .login_container {
    width: 60%;
  }
}
@media (max-width: 900px) {
  .login_container {
    width: 75%;
  }
}
@media (max-width: 600px) {
  .login_container {
    width: 100%;
  }
}
.img-logo {
  max-width: 120px;
}
.error {
  color: white;
  -webkit-animation: glow 1s ease-in-out infinite alternate;
  -moz-animation: glow 1s ease-in-out infinite alternate;
  animation: glow 1s ease-in-out infinite alternate;
  text-shadow: 0 0 20px #fff;
}
.login_input {
  color: white;
  background: none;
  border: 0;
  display: block;
  margin: 10px auto;
  text-align: center;
  border: 1px solid white;
  padding: 10px 20px;
  border-radius: 6px;
  transition: 1s all ease;
}
@media (max-width: 3000px) {
  .login_input {
    width: 55%;
  }
}
@media (max-width: 900px) {
  .login_input {
    width: 60%;
  }
}
@media (max-width: 600px) {
  .login_input {
    width: 80%;
  }
}
.login_input::placeholder {
  color: white;
}
.login_button {
  color: white;
  background: #2d3436;
  display: block;
  margin: 10px auto;
  text-align: center;
  border: 0;
  padding: 10px;
  border-radius: 6px;
  transition: 1s all ease;
}
@media (max-width: 3000px) {
  .login_button {
    width: 55%;
  }
}
@media (max-width: 900px) {
  .login_button {
    width: 60%;
  }
}
@media (max-width: 600px) {
  .login_button {
    width: 80%;
  }
}
.login_button:hover {
  background: #5de4ab;
}
.fa-facebook-square {
  color: #3b5998;
  cursor: pointer;
  margin: 8px;
  transition: 0.5s all ease;
}
.fa-facebook-square:hover {
  transform: scale(1.3);
  color: #15AABF;
}
.fa-google {
  color: #4285f4;
  cursor: pointer;
  margin: 8px;
  transition: 0.5s all ease;
}
.fa-google:hover {
  transform: scale(1.3);
  color: #e93287;
}
.fa-github {
  color: #333;
  cursor: pointer;
  margin: 8px;
  transition: 0.5s all ease;
}
.fa-github:hover {
  transform: scale(1.3);
  color: #C0E48E;
}
.signup_shell {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}
.signup_container {
  position: absolute;
  padding: 15px 0px;
  top: 50%;
  left: 50%;
  width: 60%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.signup_title {
  color: white;
}
.header_container {
  background: #364051;
}
.header_title {
  color: white;
  text-decoration: none;
}
.header_title h1 {
  margin: 0;
}
.filters_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: auto;
  margin-bottom: 2rem;
  width: 80%;
}
@media (max-width: 900px) {
  .filters_container {
    flex-wrap: wrap;
  }
}
@media (max-width: 600px) {
  .filters_container {
    flex-direction: column;
    width: 100%;
    margin-bottom: 1rem;
  }
}
.filters_container .DateRangePickerInput {
  border: 1px solid white;
  margin-bottom: 1rem;
}
.filters_container .DateRangePickerInput .DateRangePickerInput_clearDates_svg {
  margin-top: -25px;
}
.filters_container .DateRangePickerInput .DateRangePickerInput_clearDates:hover {
  background: #5de4ab;
  margin-top: -8px;
}
.filters_container .DateRangePickerInput .DateInput_input {
  border-radius: 4rem;
}
.search_box {
  background: #2d3436;
  height: 3rem;
  border-radius: 4rem;
  padding: 0.5rem;
  font-weight: 300;
  margin: auto;
  border: 1px solid white;
  margin-bottom: 1rem;
  width: 43%;
}
@media (max-width: 600px) {
  .search_box {
    margin-bottom: 0.5rem;
    width: 40%;
  }
}
@media (max-width: 900px) {
  .search_box {
    width: 80%;
  }
}
.fa-search {
  color: #5de4ab;
  float: right;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background: #2d3436;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.4s;
}
@media (max-width: 600px) {
  .fa-search {
    width: 1.5rem;
  }
}
.search_txt {
  background: none;
  border: none;
  float: left;
  padding: 0 0 0 15px;
  color: white;
  font-size: 1rem;
  transition: 1s;
  line-height: 2rem;
  width: 80;
}
.search_txt::placeholder {
  color: white;
}
.search_box:hover > .fa-search {
  background: white;
}
.search_filters {
  color: white;
  text-decoration: none;
  margin: auto;
  height: 3rem;
  width: 12%;
  padding: 10px;
  background: #2d3436;
  transition: 1s all ease;
  border: 1px solid white;
  border-radius: 4rem;
  margin-bottom: 1rem;
}
@media (max-width: 900px) {
  .search_filters {
    width: 30%;
  }
}
@media (max-width: 600px) {
  .search_filters {
    margin-bottom: 0.5rem;
    width: 80%;
  }
}
.forms_container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 2.2rem;
  max-width: 75%;
}
.forms_container > * {
  margin-bottom: 1.2rem;
}
@media (max-width: 600px) {
  .forms_container {
    max-width: 90%;
  }
}
.form_input {
  border: 1px solid white;
  height: 2rem;
  font-size: 1rem;
  font-weight: 300;
  padding: 1rem;
}
.form_text {
  border: 1px solid white;
  height: 4rem;
  font-size: 1rem;
  font-weight: 300;
  padding: 0.5em;
}
.CalendarDay__selected {
  background: #2d3436;
  color: white;
  border: 1px solid white;
}
.CalendarDay__selected:hover {
  background: #5de4ab;
  color: white;
}
.CalendarDay__default:hover {
  background: #5de4ab;
}
.bottom_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.DateInput {
  background: #2d3436;
}
.DateInput_input {
  background: #2d3436;
  color: white;
}
.DateInput_input:hover {
  background: #5de4ab;
  transition: 1s all ease;
}
.DateRangePickerInput_clearDates_svg {
  margin-top: -8px;
}
.DateRangePickerInput__showClearDates {
  background: #2d3436;
  color: white;
}
.form_btn {
  color: white;
  text-decoration: none;
  height: 3.5em;
  margin: 5px;
  width: 80%;
  padding: 10px;
  text-transform: uppercase;
  background: #2d3436;
  transition: 1s all ease;
  margin-bottom: 10px;
  border: 1px solid white;
  margin-top: 9px;
}
.form_btn:hover {
  background: #5de4ab;
}
.expense_container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  max-width: 80%;
  margin: -3rem auto 3rem auto;
}
.expense_txt {
  color: white;
  text-transform: uppercase;
  flex-basis: 100%;
}
.item_container {
  border: 1px solid white;
  width: 16rem;
  display: flex;
  flex-wrap: wrap;
  margin: auto;
  overflow: hidden;
  padding: 1rem;
  color: white;
  background: #2d3436;
  font-weight: 300;
  flex-direction: row;
  margin-bottom: 1rem;
  height: 13rem;
  justify-content: space-evenly;
}
.item_container:hover {
  transform: scale(1.1);
}
.item_description {
  flex-basis: 100%;
  font-size: 1.6rem;
}
.item_amount {
  flex-basis: 100%;
  font-size: 1rem;
}
.item_edit_btn {
  color: white;
  text-decoration: none;
  padding: 10px;
  width: 5rem;
  height: 2.5rem;
  text-transform: uppercase;
  background: #2d3436;
  transition: 1s all ease;
  margin-bottom: 10px;
  border: 1px solid white;
}
.item_edit_btn:hover {
  background: #5de4ab;
}
.item_remove_btn {
  color: white;
  text-decoration: none;
  padding: 10px;
  width: 5rem;
  height: 2.5rem;
  text-transform: uppercase;
  background: #2d3436;
  transition: 1s all ease;
  margin-bottom: 10px;
  border: 1px solid white;
}
.item_remove_btn:hover {
  background: #5de4ab;
}
.loading {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  display: flex;
  align-items: center;
}
.loading::before {
  content: "";
  position: absolute;
  width: 80px;
  height: 80px;
  background: #2d3436;
  left: 20%;
  transform: translateX(-50%) rotate(0);
  border-radius: 20px;
  animation: loading 4s infinite linear;
}
@keyframes loading {
  from {
    transform: translate(-50%) rotate(0deg);
  }
  50% {
    left: 80%;
  }
  to {
    transform: translate(-50%) rotate(1440deg);
  }
}
.notfound_page {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}
.notfound_txt {
  color: white;
  font-weight: 300;
  font-size: 2.6rem;
}
.content-container {
  margin: 0 auto;
  padding: 0 1.6rem;
  max-width: 80rem;
}
